.about {

    &__container {
        row-gap: 3.2rem;
    }

    &__img {
        width: 280px;
        justify-self: center;
    }

    &__title {
        margin-bottom: $mb-1;
    }

    &__description {
        margin-bottom: $mb-2;
    }

    &__details {
        display: grid;
        row-gap: 1.6rem;
        margin-bottom: $mb-2-5;

        &-description {
            display: inline-flex;
            column-gap: 8px;
            font-size: $small-font-size;
        }

        &-icon {
            font-size: 1.6rem;
            color: $primary-color;
            margin-top: 2px;
        }
    }
}